// eslint-disable-next-line 

import React, {useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';

let 박스 = styled.div`
    padding : 20px;
`;
let 제목 = styled.h4`
    font-size : 25px;
    color : ${props => props.색상};
`;

// class Detail2 extends React.Component {
//   componentDidMount()
//   {

//   }
//   componentWillUnmount() {

//   }
// }
function Detail (props){
  let[view, chView] = useState(true);
    let [tmp, chTmp] = useState('');
  useEffect ( () => {
    let timer = setTimeout(() => { //setTimeout 사용시 주의사항
      chView(false);
    }, 2000);
    console.log("hi")
    return () => {
      clearTimeout(timer) //타이머 초기화를 안해주면 버그 발생
    } //컴포넌트가 사라질때 실행됨 function test(){} 사용도 가능
    //useEffect 는 여려개 생성 가능
  },[]); //안에 조건을 넣을 수 있음 state가 들어가면 state가 변경될때만 실행
  //[] 빈칸이면 component가 등장할때만 한번 실행하고 끝남

  
    let history = useHistory();
    let {id} = useParams();
    let findProduct = props.useShoes.find(function(shoes)
    {
        return shoes.id == id
    })
    return (
    <div className="container">
        <박스>
        <제목 색상="red">상세 페이지</제목>
        </박스>
        {tmp}
        <input onChange={(e) => {chTmp(e.target.value)}}/>
    <div className="row">
      <div className="col-md-6">
        {view == true
        ? <img src={"https://codingapple1.github.io/shop/shoes" +(findProduct.id+1)+".jpg"} width="100%" />
        : null
      }
        
      </div>
      <div className="col-md-6 mt-4">
        <h4 className="pt-5">{findProduct.title}</h4>
        <p>{findProduct.content}</p>
        <p>{findProduct.price}</p>
        <button className="btn btn-danger">주문하기</button> 
        <button className="btn btn-danger" onClick={() =>{history.push('/')}}>뒤로가기</button> 
      </div>
    </div>
    </div> )
}

export default Detail;